import { NextResponse } from "next/server";

export const dynamic = "force-dynamic";

export async function GET() {
    try {
        const res = await fetch("https://www.iitp.kr/board-svc/api/bbs/A/list.do", {
            method: "POST",
            headers: {
                "Content-Type": "application/json",
                "Accept": "application/json, text/plain, */*",
                "Referer": "https://www.iitp.kr/web/lay1/bbs/S1T12C38/A/8/list.do",
                "Origin": "https://www.iitp.kr",
                "User-Agent": "Mozilla/5.0 (Windows NT 10.0; Win64; x64) AppleWebKit/537.36 Chrome/120.0.0.0 Safari/537.36",
            },
            body: JSON.stringify({
                cms_menu_seq: "38",
                cpage: 1,
                rows: "3",
                keyword: "",
                condition: "",
                sort: "latest",
            }),
            signal: AbortSignal.timeout(20_000),
        });

        const json = await res.json();
        // 첫 번째 항목의 모든 필드를 확인
        const firstItem = json.list?.[0] ?? null;

        return NextResponse.json({
            status: res.status,
            result: json.result,
            firstItemKeys: firstItem ? Object.keys(firstItem) : [],
            firstItem,
            totalCount: json.pagination?.totalcount,
        });
    } catch (err: any) {
        return NextResponse.json({ error: err.message }, { status: 500 });
    }
}
